import { CommunityEntry, SubmissionParams, ModerationStatus } from "../types/communityTypes";

// Configuration Cloudinary
const CLOUDINARY_CLOUD_NAME = 'dpatqkgsc';
const CLOUDINARY_API_URL = `https://api.cloudinary.com/v1_1/${CLOUDINARY_CLOUD_NAME}`;

// Configuration Firebase pour synchronisation temps rel
const FIREBASE_CONFIG = {
  databaseURL: "https://collectif-feydeau-default-rtdb.europe-west1.firebasedatabase.app"
};

/**
 *  Rcupre toutes les photos depuis Cloudinary (INSTANTAN)
 */
export async function fetchCommunityEntries(): Promise<CommunityEntry[]> {
  try {
    //  TABLE RASE : Effacer toutes les anciennes contributions au premier chargement SEULEMENT
    const isFirstLoad = !localStorage.getItem('cloudinary_revolution_started');
    if (isFirstLoad) {
      localStorage.removeItem('community_entries');
      localStorage.setItem('cloudinary_revolution_started', 'true');
      console.log('[CloudinaryService]  Systme initialis v2.1.0');
    }
    
    //  FIREBASE PUR : Une seule source de vrit !
    const response = await fetch(`${FIREBASE_CONFIG.databaseURL}/community-photos.json`);
    
    if (!response.ok) {
      console.error('[CloudinaryService]  Firebase erreur:', response.status);
      return [];
    }
    
    const firebaseData = await response.json();
    const firebaseEntries = firebaseData ? Object.values(firebaseData) as CommunityEntry[] : [];
    
    console.log(`[CloudinaryService]  ${firebaseEntries.length} photos charges`);
    
    return firebaseEntries.sort((a: CommunityEntry, b: CommunityEntry) => 
      new Date(b.createdAt).getTime() - new Date(a.createdAt).getTime()
    );
    
  } catch (error) {
    console.error('[CloudinaryService] Erreur:', error);
    return [];
  }
}

/**
 *  RVOLUTION : Effacer toutes les anciennes contributions
 */
export function clearAllContributions(): void {
  console.log('[CloudinaryService]  TABLE RASE : Suppression de toutes les contributions !');
  localStorage.removeItem('community_entries');
  console.log('[CloudinaryService]  Toutes les contributions supprimes ! Nouveau dpart !');
}

/**
 *  Soumission instantane (0 seconde !)
 */
export async function submitContribution(params: SubmissionParams): Promise<CommunityEntry> {
  const finalImageUrl = params.cloudinaryUrl || params.imageUrl;
  if (!finalImageUrl) {
    throw new Error('URL Cloudinary manquante');
  }

  // Extraire le public_id depuis l'URL Cloudinary
  const publicIdMatch = finalImageUrl.match(/\/v\d+\/([^\.]+)/);
  const publicId = publicIdMatch ? publicIdMatch[1] : `photo_${Date.now()}`;

  // Crer l'entre
  const newEntry: CommunityEntry = {
    id: publicId,
    type: params.type,
    displayName: params.displayName?.trim() || 'Anonyme',
    content: params.content?.trim() || '',
    imageUrl: finalImageUrl,
    thumbnailUrl: finalImageUrl,
    description: params.description?.trim() || '',
    createdAt: new Date().toISOString(),
    timestamp: new Date().toISOString(),
    moderation: {
      status: 'approved' as ModerationStatus,
      moderatedAt: new Date().toISOString()
    }
  };
  
  //  SAUVEGARDER dans Firebase
  const response = await fetch(`${FIREBASE_CONFIG.databaseURL}/community-photos/${newEntry.id}.json`, {
    method: 'PUT',
    headers: { 'Content-Type': 'application/json' },
    body: JSON.stringify(newEntry)
  });
  
  if (!response.ok) {
    throw new Error('Impossible de sauvegarder la photo. Vrifiez votre connexion.');
  }
  
  console.log('[CloudinaryService]  Photo sauvegarde');
  
  // Toast instantan de succs
  if (typeof window !== 'undefined') {
    // Dclencher un vnement pour le toast
    window.dispatchEvent(new CustomEvent('community-photo-added', {
      detail: { 
        message: ' Photo ajoute instantanment !',
        description: 'Votre photo est visible immdiatement sur tous les appareils.',
        type: 'success'
      }
    }));
  }
  
  return newEntry;
}

/**
 *  Suppression admin via tags Cloudinary
 */
export async function deleteCommunityEntry(entryId: string): Promise<void> {
  //  SUPPRESSION dans Firebase
  const response = await fetch(`${FIREBASE_CONFIG.databaseURL}/community-photos/${entryId}.json`, {
    method: 'DELETE'
  });
  
  if (!response.ok) {
    throw new Error('Impossible de supprimer la photo');
  }
  
  console.log('[CloudinaryService]  Photo supprime');
}
