/**
 * Service de suivi des erreurs
 * Ce service permet de collecter des informations sur les erreurs rencontres
 * par les utilisateurs et de les envoyer  un service de suivi
 */

// Type pour les erreurs collectes
export interface ErrorInfo {
  message: string;
  stack?: string;
  url: string;
  timestamp: string;
  userAgent: string;
  path: string;
  componentName?: string;
  additionalInfo?: Record<string, any>;
}

// Stockage local des erreurs
const ERROR_STORAGE_KEY = 'app_errors';
const MAX_STORED_ERRORS = 50;

/**
 * Capturer une erreur et la stocker localement
 */
export const captureError = (
  error: Error | string,
  componentName?: string,
  additionalInfo?: Record<string, any>
): void => {
  try {
    const message = typeof error === 'string' ? error : error.message;
    const stack = typeof error === 'string' ? undefined : error.stack;
    
    const errorInfo: ErrorInfo = {
      message,
      stack,
      url: window.location.href,
      timestamp: new Date().toISOString(),
      userAgent: navigator.userAgent,
      path: window.location.pathname,
      componentName,
      additionalInfo
    };
    
    // Rcuprer les erreurs existantes
    const storedErrorsStr = localStorage.getItem(ERROR_STORAGE_KEY);
    let storedErrors: ErrorInfo[] = [];
    
    if (storedErrorsStr) {
      try {
        storedErrors = JSON.parse(storedErrorsStr);
        if (!Array.isArray(storedErrors)) storedErrors = [];
      } catch (e) {
        console.error('Erreur lors de la lecture des erreurs stockes:', e);
        storedErrors = [];
      }
    }
    
    // Ajouter la nouvelle erreur
    storedErrors.push(errorInfo);
    
    // Limiter le nombre d'erreurs stockes
    if (storedErrors.length > MAX_STORED_ERRORS) {
      storedErrors = storedErrors.slice(-MAX_STORED_ERRORS);
    }
    
    // Sauvegarder les erreurs
    localStorage.setItem(ERROR_STORAGE_KEY, JSON.stringify(storedErrors));
    
    // Afficher l'erreur dans la console pour le dbogage
    console.error('Erreur capture:', errorInfo);
  } catch (e) {
    console.error('Erreur lors de la capture d\'erreur:', e);
  }
};

/**
 * Rcuprer toutes les erreurs stockes
 */
export const getStoredErrors = (): ErrorInfo[] => {
  try {
    const storedErrorsStr = localStorage.getItem(ERROR_STORAGE_KEY);
    if (!storedErrorsStr) return [];
    
    const storedErrors = JSON.parse(storedErrorsStr);
    return Array.isArray(storedErrors) ? storedErrors : [];
  } catch (e) {
    console.error('Erreur lors de la rcupration des erreurs stockes:', e);
    return [];
  }
};

/**
 * Effacer toutes les erreurs stockes
 */
export const clearStoredErrors = (): void => {
  localStorage.removeItem(ERROR_STORAGE_KEY);
};

// Configuration EmailJS
const EMAIL_SERVICE_ID = 'service_3l31ox3';
const EMAIL_TEMPLATE_ID = 'template_q7nh8h2';

/**
 * Initialiser EmailJS
 *  appeler au dmarrage de l'application
 */
export const initEmailJS = async (): Promise<void> => {
  try {
    const emailjs = await import('@emailjs/browser');
    // Vous n'avez pas besoin de spcifier le USER_ID ici
    // EmailJS le rcuprera depuis le script charg dans index.html
  } catch (error) {
    console.error('Erreur lors de l\'initialisation d\'EmailJS:', error);
  }
};

/**
 * Vrifier et envoyer les erreurs automatiquement
 *  appeler priodiquement ou lors d'vnements spcifiques
 */
export const checkAndSendErrors = async (): Promise<void> => {
  const errors = getStoredErrors();
  
  // Envoyer les erreurs si leur nombre dpasse un certain seuil
  if (errors.length >= 5) {
    await sendErrorsToTrackingService();
  } else if (errors.length > 0) {
    console.log(`${errors.length} erreur(s) en attente d'envoi`);
  }
};

/**
 * Envoyer les erreurs stockes  un service de suivi via EmailJS
 */
export const sendErrorsToTrackingService = async (): Promise<boolean> => {
  try {
    const errors = getStoredErrors();
    if (errors.length === 0) return true;
    
    // Vrifier si nous sommes en production
    const isProd = import.meta.env.PROD;
    
    if (isProd) {
      console.log(`Envoi de ${errors.length} erreurs par email`);
      
      // Importer EmailJS dynamiquement
      const emailjs = await import('@emailjs/browser');
      
      // Prparer les donnes pour le modle d'email
      const templateParams = {
        errors_json: JSON.stringify(errors, null, 2),
        error_count: errors.length,
        timestamp: new Date().toISOString(),
        user_agent: navigator.userAgent,
        app_version: '1.0.0', //  mettre  jour avec la version de votre application
        app_name: 'Collectif Feydeau'
      };
      
      // Envoyer l'email avec la cl publique explicite
      // Mme si elle est dj initialise dans index.html, la fournir ici pour s'assurer qu'elle est disponible
      const PUBLIC_KEY = 'HoNWMyqrINGzjeK6E';
      const response = await emailjs.send(
        EMAIL_SERVICE_ID,
        EMAIL_TEMPLATE_ID,
        templateParams,
        PUBLIC_KEY
      );
      
      if (response.status === 200) {
        console.log('Erreurs envoyes avec succs');
        clearStoredErrors();
        return true;
      } else {
        throw new Error(`Erreur lors de l'envoi: ${response.text}`);
      }
    } else {
      // En dveloppement, juste afficher les erreurs
      console.log('Mode dveloppement - Simulation d\'envoi de', errors.length, 'erreurs');
      console.table(errors);
      clearStoredErrors();
      return true;
    }
  } catch (e) {
    console.error('Erreur lors de l\'envoi des erreurs:', e);
    return false;
  }
};

/**
 * Configurer un gestionnaire global d'erreurs non captures
 */
export const setupGlobalErrorHandler = (): void => {
  window.onerror = (message, source, lineno, colno, error) => {
    captureError(
      error || String(message),
      'GlobalErrorHandler',
      { source, lineno, colno }
    );
    return false; // Permettre au gestionnaire d'erreurs par dfaut de s'excuter galement
  };
  
  window.addEventListener('unhandledrejection', (event) => {
    captureError(
      event.reason || 'Promesse rejete non gre',
      'UnhandledRejection'
    );
  });
};

/**
 * Crer un composant d'erreur pour React Error Boundary
 */
export const createErrorBoundaryHandler = (componentName: string) => {
  return (error: Error, info: { componentStack: string }) => {
    captureError(error, componentName, { componentStack: info.componentStack });
  };
};
